<template lang="pug">
	#app
		home
		.wrapper-router(ref="teste")
			main-nav
			.container
				router-view
		.footer-app
			.container
				.contato
					h3.title Dúvidas? Ligue 98 - 21096410
					h3.title Nosso email: eventos@taguaturturismo.com.br

				.apoios-patrocinios
					.group.apoios
						span.title Apoios
						ul.list
							li.item.sboc SBOC

					.group.patrocinadores
						span.title Patrocinadores
						ul.list
							li.item Google

				.social
					span.title Redes sociais
					ul.list
						li.item.facebook Facebook
						li.item.twitter Twitter
						li.item.instagram Instagram
						li.item.youtube Youtube

</template>

<script>
	// @ is an alias to /src
	import Home from '@/views/Home.vue'
	import MainNav from '@/components/MainNav.vue'

	import rallax from 'rallax.js'

	export default {
		data(){
			return{
				scrolled: 0
			}
		},
		components: {
			MainNav,
			Home
		},
		computed:{
			currentRouter(){
				return this.$store.state.currentRouter
			}
		},
		methods: {
			handleScroll () {
				this.scrolled = window.scrollY > 0;
				this.goParticipantes();
			},
			goParticipantes() {
				if (this.$route.name == 'home') {
					if (this.scrolled) {
						
						if (this.currentRouter != 'home') {
							this.$router.push({
								name: this.currentRouter
							});
						}else{
							this.$router.push({
								name: 'palestrantes'
							});
						}
					}else{
						this.$store.commit('SET_CURRENT_ROUTER', this.$route.name);

						this.$router.push({
							name: 'home'
						});
					}
				}else{
					if (!this.scrolled && this.$route.name != 'participante') {
						this.$store.commit('SET_CURRENT_ROUTER', this.$route.name);
						
						this.$router.push({
							name: 'home'
						});
					}
				}

			}
		},
		created () {
			window.addEventListener('scroll', this.handleScroll);
			this.goParticipantes();

			
		},
		destroyed () {
			window.removeEventListener('scroll', this.handleScroll);
		}
	}
</script>


<style lang="stylus" src="@/assets/styles/App.styl"></style>